/*global kakao*/
import React, { Component } from "react";
import { Query } from "react-apollo";
import AddressModal from "../utils/daumPostcode";
import { Mutation } from "react-apollo";
import gql from "graphql-tag";
import ErrorNotLogin from "../basics/ErrorNotLogin";

const USER_DATA_QUERY = gql`
  query {
    me {
      email
      nickName
      userName
      gender
      birth
      address1
      address2
      id
      userName
      address1
    }
  }
`;

const USER_EDIT_MUTATION = gql`
  mutation editUser( $nickName: String! $address1: String! $address2: String! $lat: Float! $lon: Float! $phone: String!){
    editUser(nickName: $nickName, address1: $address1 address2: $address2 lat: $lat lon: $lon phone: $phone)
  }
`
class UserEdit extends Component {
  state = {
    email: "",
    emailComplete: false,
    emailNotComplete: false,
    emailAuthenticationValue: false, //  
    emailUserInput: null, //    
    password: "",
    userName: "",
    nickName: "",
    gender: "",
    phone: "",
    address1: "",
    address2: "",
    birth: "",
    lat: Number,
    lon: Number,
    firstInData: false,
  };
  render() {
    const API_KEY = process.env.REACT_APP_KAKAO_API_KEY;

    const takeAddress = (takeAddress) => {
      this.setState({
        address1: takeAddress,
      });
      getLatLon();
    };

    const getLatLon = () => {
      const mapScript = document.createElement("script");
      mapScript.async = true;
      mapScript.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${API_KEY}&autoload=false&libraries=services`;
      document.head.appendChild(mapScript);
      mapScript.onload = () => {
        kakao.maps.load(() => {
          //     
          // -   
          const geocoder = new kakao.maps.services.Geocoder();
          geocoder.addressSearch(this.state.address1, function (result, status) {
            //   
            if (status === kakao.maps.services.Status.OK) {
              let lat = Number(result[0].y); // 
              let lon = Number(result[0].x); // 
              lonlatstate(lat, lon);
            }
          });
        });
      };
      return <></>;
    };

    const lonlatstate = (klat, klon) => {
      this.setState({
        lat: klat,
        lon: klon,
      });
    };

    return (
      <>
        <h2>  </h2>
        <Query query={USER_DATA_QUERY}>
          {({ loading, error, data }) => {
            if (loading){
              return (
                <div>Loading...
                </div>
              )
            }
            if (error){return <ErrorNotLogin />}
            const myData = data
            const firstInData = this.state.firstInData
            if(firstInData === false){
              this.state = myData.me
              this.state.firstInData = true
            }
            const {
              email,
              password,
              userName,
              nickName,
              gender,
              phone,
              address1,
              address2,
              birth,
              lat,
              lon,
            } = this.state;
            return (
              <>
                <div className="signup">
                  <form>
                    <div className="signupform">
                      <img alt="" className="nomargin" src="signupcat.png" width="80px"></img>
                      <div className="email">
                        <input
                          className="signupinput1"
                          value={this.state.email}
                          onChange={(e) => this.setState({ email: e.target.value })}
                          type="text"
                          placeholder="  "
                        />
                      </div>
                    </div>
                  </form>
                  <form>
                    <div className="signupform2">
                      <div className="nickName">
                        <input
                          className="signupinput3"
                          value={nickName}
                          onChange={(e) => this.setState({ nickName: e.target.value })}
                          type="text"
                          placeholder="  "
                        />
                      </div>
                      <div className="userName">
                        <input
                          value={userName}
                          onChange={(e) => this.setState({ userName: e.target.value })}
                          type="text"
                          placeholder="  "
                        />
                      </div>
                    </div>
                  </form>
                  <form>
                    <div className="signupform3">
                      <div className="gender">
                        <label>
                          <input
                            value="M"
                            onChange={(e) => this.setState({ gender: e.target.value })}
                            type="radio"
                            name="gender"
                          />{" "}
                          
                        </label>
                        <label>
                          <input
                            value="W"
                            onChange={(e) => this.setState({ gender: e.target.value })}
                            type="radio"
                            name="gender"
                          />{" "}
                          
                        </label>
                      </div>
                      <div className="birth">
                        <input
                          value={birth}
                          onChange={(e) => this.setState({ birth: e.target.value })}
                          type="text"
                          placeholder=" - &nbsp;&nbsp;&nbsp;&nbsp;ex)971006"
                        />
                      </div>
                      <div className="phone">
                        <input
                          value={phone}
                          onChange={(e) => this.setState({ phone: e.target.value })}
                          type="text"
                          placeholder=" - &nbsp;&nbsp;&nbsp;&nbsp;ex)01012345678"
                        />
                      </div>
                      <div className="address">
                        <input
                          value={address1}
                          readOnly
                          type="text"
                          placeholder="  "
                        />
                        <AddressModal refreshFunction={takeAddress.bind(this)} />
                      </div>
                      <div className="address">
                        <input
                          value={address2}
                          onChange={(e) => this.setState({ address2: e.target.value })}
                          type="text"
                          placeholder=" "
                        />
                      </div>
                      <div>
                        <Mutation
                          mutation={USER_EDIT_MUTATION}
                          variables={{
                            nickName,
                            phone,
                            address1,
                            address2,
                            lat,
                            lon,
                          }}
                          onCompleted={(data) => this._confirm(data)}
                        >
                          {(mutation) => (
                            <input className="submit" onClick={mutation} readOnly value=""></input>
                          )}
                        </Mutation>
                        <input
                          type="reset"
                          className="reset"
                          onClick={this.reload}
                          readOnly
                          value=""
                        ></input>
                      </div>
                    </div>
                  </form>
                </div>
              </>
            )
          }
        }
        </ Query>
      </>
    );
  }
  _confirm = async (data) => {
    window.alert(" .")
    this.props.history.push(`/userInfor`);
  };
}

export default UserEdit;
