import React, { Component } from "react";
import { Query } from "react-apollo";
import gql from "graphql-tag";
import { Link } from "react-router-dom";
import { Mutation } from "react-apollo";
import Moment from "react-moment";

const BOARD_DETAIL_QUERY = gql`
  query detailService($serviceId: Int!) {
    detailService(serviceId: $serviceId) {
      service {
        id
        title
        contents
        price
        address1
        address2
        lat
        lon
        startAt
        endAt
        progress
        reqUser {
          id
          userName
          nickName
        }
        ansUser {
          id
          userName
          nickName
        }
        serviceimgfiles {
          id
          imglink
        }
      }
    }
  }
`;

const USER_DATA_QUERY = gql`
  query {
    me {
      id
      email
      userName
      address1
      address2
    }
  }
`;

const DELETE_SERVICE_BOARD_MUTATION = gql`
  mutation DeleteServiceBoardMutation($serviceId: Int!) {
    removeService(id: $serviceId)
  }
`;

const SERVICE_ACCEPT_MUTATION = gql`
  mutation ServiceAcceptMutation($serviceId: Int!) {
    giveService(id: $serviceId) {
      id
    }
  }
`;

class BoardDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      requester: "",
      acceptor: "",
      requesterTrue: false,
    };
  }

  render() {
    const paramsId = Number(this.props.match.params.id);
    return (
      <>
        <Query
          query={BOARD_DETAIL_QUERY}
          variables={{
            serviceId: paramsId,
          }}
        >
          {({ loading, error, data }) => {
            if (loading)
              return (
                <>
                  <div className="map2">Loading...</div>
                </>
              );
            if (error)
              return <></>;
            const serviceBoardData = data.detailService.service; //  
            const serviceId = Number(serviceBoardData.id);
            const startAt = new Date(serviceBoardData.startAt);
            const endAt = new Date(serviceBoardData.endAt);
            const { serviceimgfiles } = serviceBoardData;
            this.state.requester = serviceBoardData.reqUser.id;
            return (
              <>
                <Query query={USER_DATA_QUERY}>
                  {({ loading, error, data }) => {
                    if (loading)
                      return (
                        <>
                          <div className="map2">Loading...</div>
                        </>
                      );
                    if (error)
                      // 
                      return (
                        <>
                          <div className="writeform33">
                            <img
                              alt=""
                              className="nomargin"
                              src="/boardcat.png"
                              width="80px"
                            ></img>
                            <div className="nolog-title2"></div>
                            <input
                              className="writetitleinput"
                              value={serviceBoardData.title}
                              readOnly
                            ></input>
                            <br></br>
                            <div className="nolog-title"></div>
                            <textarea
                              className="writecontentinput"
                              value={serviceBoardData.contents}
                              readOnly
                            ></textarea>
                            <div className="imgbox">
                              {serviceimgfiles.map((data, i) => {
                                return (
                                  <img
                                    className="detailimg"
                                    key={i}
                                    alt=" "
                                    src={data.imglink}
                                  />
                                );
                              })}
                            </div>
                            <br></br>
                            <div className="nolog-title"></div>
                            <input
                              className="writetitleinput"
                              value={serviceBoardData.price}
                              readOnly
                            ></input>
                            <br></br>
                            <div className="nolog-title"></div>
                            <input
                              className="writetitleinput"
                              value={
                                serviceBoardData.address1 +
                                ", " +
                                serviceBoardData.address2
                              }
                              readOnly
                            ></input>
                            <br></br>
                            <div className="nolog-title">&nbsp;</div>
                            <Moment format="YYYY&nbsp;MM&nbsp;DD&nbsp;hh&nbsp;mm">
                              {startAt}
                            </Moment>
                            <div className="nolog-title">&nbsp;</div>
                            <Moment format="YYYY&nbsp;MM&nbsp;DD&nbsp;hh&nbsp;mm">
                              {endAt}
                            </Moment>
                            <input
                              className="writereset2"
                              type="reset"
                              value=""
                              readOnly
                              onClick={this.Goback}
                            ></input>
                          </div>
                        </>
                      );
                    //   id  id   true
                    if (data) {
                      this.state.me = data.me.id;
                      if (this.state.requester === this.state.me) {
                        this.state.requesterTrue = true;
                      }
                    }
                    // 
                    return (
                      <>
                        <div className="writeform33">
                          <img
                            alt=""
                            className="nomargin"
                            src="/boardcat.png"
                            width="80px"
                          ></img>
                          <div className="nolog-title2"></div>
                          <input
                            className="writetitleinput"
                            value={serviceBoardData.title}
                            readOnly
                          ></input>
                          <br></br>
                          <div className="nolog-title"></div>
                          <textarea
                            className="writecontentinput"
                            value={serviceBoardData.contents}
                            readOnly
                          ></textarea>
                          <br></br>
                          <div className="imgbox">
                            {serviceimgfiles.map((data, i) => {
                              return (
                                <img
                                  className="detailimg"
                                  key={i}
                                  alt=" "
                                  src={data.imglink}
                                />
                              );
                            })}
                          </div>
                          <div className="nolog-title"></div>
                          <input
                            className="writetitleinput"
                            value={serviceBoardData.price}
                            readOnly
                          ></input>
                          <br></br>
                          <div className="nolog-title"></div>
                          <input
                            className="writetitleinput"
                            value={
                              serviceBoardData.address1 +
                              ", " +
                              serviceBoardData.address2
                            }
                            readOnly
                          ></input>
                          <br></br>
                          <div className="nolog-title"> </div>
                          <Moment format="YY MM DD hh mm">
                            {startAt}
                          </Moment>
                          <div className="nolog-title"> </div>
                          <Moment format="YY MM DD hh mm">
                            {endAt}
                          </Moment>
                          <input
                            className="writereset3"
                            type="reset"
                            value=""
                            readOnly
                            onClick={this.Goback}
                          ></input>
                          {serviceBoardData.progress ? ( //    
                            <></>
                          ) : serviceBoardData.ansUser ? ( //   
                            this.state.requesterTrue ? ( //    
                              <Link
                                to={{
                                  pathname: `/reviewWrite`,
                                  serviceBoardData: serviceBoardData,
                                  serviceBoardId: serviceId,
                                  userData: data,
                                }}
                              >
                                <button className="writereset"></button>
                              </Link>
                            ) : (
                              <></>
                            ) //      
                          ) : this.state.requesterTrue ? ( //   
                            <>
                              <a href="/board">
                                <Mutation
                                  mutation={DELETE_SERVICE_BOARD_MUTATION}
                                  variables={{ serviceId }}
                                >
                                  {(mutation) => (
                                    <button
                                      className="writereset"
                                      onClick={mutation}
                                    >
                                      
                                    </button>
                                  )}
                                </Mutation>
                              </a>
                              <Link
                                to={{
                                  pathname: `/board/update/${serviceId}`,
                                  serviceBoardData: serviceBoardData,
                                }}
                              >
                                <input
                                  className="writereset"
                                  type="reset"
                                  value=""
                                  readOnly
                                ></input>
                              </Link>
                            </>
                          ) : (
                            //     
                            <>
                              <a href="/board">
                                <Mutation
                                  mutation={SERVICE_ACCEPT_MUTATION}
                                  variables={{ serviceId }}
                                >
                                  {(mutation) => (
                                    <button
                                      className="writereset"
                                      onClick={mutation}
                                    >
                                      
                                    </button>
                                  )}
                                </Mutation>
                              </a>
                            </>
                          )}
                        </div>
                      </>
                    );
                  }}
                </Query>
              </>
            );
          }}
        </Query>
      </>
    );
  }
  Goback() {
    window.history.go(-1);
  }
}

export default BoardDetail;
