/*global kakao*/
import React, { Component } from "react";
import { AUTH_TOKEN } from "../../constants";
import { Mutation } from "react-apollo";
import gql from "graphql-tag";
import AddressModal from "../utils/daumPostcode";

const SIGNUP_MUTATION = gql`
  mutation signup(
    $email: Email!
    $password: String!
    $userName: String!
    $nickName: String!
    $gender: String!
    $phone: String!
    $address1: String!
    $address2: String!
    $birth: String!
    $lat: Float!
    $lon: Float!
  ) {
    signup(
      email: $email
      password: $password
      userName: $userName
      nickName: $nickName
      gender: $gender
      phone: $phone
      address1: $address1
      address2: $address2
      birth: $birth
      lat: $lat
      lon: $lon
    ) {
      token
    }
  }
`;

const SEND_EMAIL_MUTATION = gql`
  mutation SendEmailMutation($email: String!) {
    sendEmail(email: $email)
  }
`;

class SignupPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      emailComplete: false,
      emailNotComplete: false,
      emailAuthenticationValue: false, //  
      emailUserInput: null, //    
      password: "",
      passwordCheck: "",
      userName: "",
      nickName: "",
      gender: "",
      phone: "",
      address1: "",
      address2: "",
      birth: "",
      lat: Number,
      lon: Number,
    };
  }

  emailValueChecking() {
    if (this.state.emailUserInput === this.state.emailAuthenticationValue) {
      this.setState({
        emailComplete: true,
      });
    } else {
      this.setState({
        emailNotComplete: true,
      });
    }
  }


  render() {
    const API_KEY = process.env.REACT_APP_KAKAO_API_KEY;
    const {
      email,
      password,
      passwordCheck,
      userName,
      nickName,
      gender,
      phone,
      address1,
      address2,
      birth,
      lat,
      lon,
    } = this.state;

    const takeAddress = (takeAddress) => {
      this.setState({
        address1: takeAddress,
      });
      getLatLon();
    };

    const getLatLon = () => {
      const mapScript = document.createElement("script");
      mapScript.async = true;
      mapScript.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${API_KEY}&autoload=false&libraries=services`;
      document.head.appendChild(mapScript);
      mapScript.onload = () => {
        kakao.maps.load(() => {
          //     
          // -   
          const geocoder = new kakao.maps.services.Geocoder();
          geocoder.addressSearch(this.state.address1, function (result, status) {
            //   
            if (status === kakao.maps.services.Status.OK) {
              let lat = Number(result[0].y); // 
              let lon = Number(result[0].x); // 
              lonlatstate(lat, lon);
            }
          });
        });
      };
      return <></>;
    };

    const lonlatstate = (klat, klon) => {
      this.setState({
        lat: klat,
        lon: klon,
      });
    };

    console.log(this.state.emailAuthenticationValue);
    return (
      <>
        <div className="signup">
          <form>
            <div className="signupform">
              <img alt="" className="nomargin" src="signupcat.png" width="80px"></img>
              <div className="email">
                {!this.state.emailComplete ?
                <input
                  className="signupinput1"
                  value={email}
                  onChange={(e) => this.setState({ email: e.target.value })}
                  type="text"
                  placeholder="  "
                />
                :
                <input
                  className="signupinput1"
                  value={email}
                  onChange={(e) => this.setState({ email: e.target.value })}
                  type="text"
                  readOnly
                />
                }
              </div>
              <Mutation
                mutation={SEND_EMAIL_MUTATION}
                variables={{ email }}
                onCompleted={(data) => this._sendconfirm(data)}
              >
                {(mutation) => (
                  <>
                    {this.state.emailAuthenticationValue ? (
                      <>
                      {!this.state.emailComplete ? (
                        <>
                        <h1> !</h1>
                        <input
                          className="mailsubmit"
                          readOnly
                          onClick={mutation}
                          value=""
                        ></input>
                        </>
                      ): ""
                      }
                      </>
                    ) : (
                      <input
                        className="mailsubmit"
                        readOnly
                        onClick={mutation}
                        value=" "
                      ></input>
                    )}
                  </>
                )}
              </Mutation>
              {!this.state.emailComplete ?
              <div className="email">
                <input
                  className="signupinput2"
                  onChange={(e) => this.setState({ emailUserInput: e.target.value })}
                  type="text"
                  placeholder="  "
                />
              </div>
              :""
              }
              {!this.state.emailComplete ? ( //   
                <>
                  {this.state.emailNotComplete ? ( //     
                    <div>  .</div>
                  ) : (
                    ""
                  )}
                  <input
                    className="mailsubmit"
                    readOnly
                    value=" "
                    onClick={this.emailValueChecking.bind(this)}
                  ></input>
                </>
              ) : (
                <div> .!</div>
              )}
            </div>
          </form>
          <form>
            <div className="signupform2">
              <div className="nickName">
                <input
                  className="signupinput3"
                  value={nickName}
                  onChange={(e) => this.setState({ nickName: e.target.value })}
                  type="text"
                  placeholder="  "
                />
              </div>
              <div className="userName">
                <input
                  value={userName}
                  onChange={(e) => this.setState({ userName: e.target.value })}
                  type="text"
                  placeholder="  "
                />
              </div>
              <div className="password">
                <input
                  value={password}
                  onChange={(e) => this.setState({ password: e.target.value })}
                  type="password"
                  placeholder=" "
                />
              </div>
              <div className="passwordcheck">
                <input
                type="password"
                placeholder=" "
                onChange={(e) => this.setState({ passwordCheck: e.target.value })}
                ></input>
                {password && passwordCheck ?
                  <div>{password === passwordCheck ? <span className="colorblue">.</span> :<span className="colorblack"> .</span>}</div>
                  : ""
                }
              </div>
            </div>
          </form>
          <form>
            <div className="signupform3">
              <div className="gender">
                <label>
                  <input
                    value="M"
                    onChange={(e) => this.setState({ gender: e.target.value })}
                    type="radio"
                    name="gender"
                  />{" "}
                  
                </label>
                <label>
                  <input
                    value="W"
                    onChange={(e) => this.setState({ gender: e.target.value })}
                    type="radio"
                    name="gender"
                  />{" "}
                  
                </label>
              </div>
              <div className="birth">
                <input
                  value={birth}
                  onChange={(e) => this.setState({ birth: e.target.value })}
                  type="text"
                  placeholder=" - &nbsp;&nbsp;&nbsp;&nbsp;ex)971006"
                />
              </div>
              <div className="phone">
                <input
                  value={phone}
                  onChange={(e) => this.setState({ phone: e.target.value })}
                  type="text"
                  placeholder=" - &nbsp;&nbsp;&nbsp;&nbsp;ex)01012345678"
                />
              </div>
              <div className="address">
                <input
                  value={address1}
                  readOnly
                  type="text"
                  placeholder="  "
                />
                <AddressModal refreshFunction={takeAddress.bind(this)} />
              </div>
              <div className="address">
                <input
                  value={address2}
                  onChange={(e) => this.setState({ address2: e.target.value })}
                  type="text"
                  placeholder=" "
                />
              </div>
              <div>
                {this.state.emailComplete ? (
                  <Mutation
                    mutation={SIGNUP_MUTATION}
                    variables={{
                      email,
                      password,
                      userName,
                      nickName,
                      gender,
                      phone,
                      address1,
                      address2,
                      birth,
                      lat,
                      lon,
                    }}
                    onCompleted={(data) => this._confirm(data)}
                  >
                    {(mutation) => (
                      <input className="submit" onClick={mutation} readOnly value=""></input>
                    )}
                  </Mutation>
                ) : (
                  <input
                    className="submit"
                    onClick={this.emailAlert.bind(this)}
                    readOnly
                    value=""
                  ></input>
                )}
                <input
                  type="reset"
                  className="reset"
                  onClick={this.reload}
                  readOnly
                  value=""
                ></input>
              </div>
            </div>
          </form>
        </div>
      </>
    );
  }
  emailAlert() {
    alert("  .");
  }
  reload() {
    window.location.reload();
  }
  _sendconfirm = async (data) => {
    this.setState({
      emailAuthenticationValue: data.sendEmail,
    });
  };

  _confirm = async (data) => {
    const tokendata = data.signup.token;
    this._saveUserData(tokendata);
    this.props.history.push(`/`);
  };

  _saveUserData = (tokendata) => {
    localStorage.setItem(AUTH_TOKEN, tokendata);
  };
}

export default SignupPage;
