import React, { Component } from "react";
import { Link } from "react-router-dom";

class UserBoardArticle extends Component {
  render() {
    const serviceBoardData = this.props.serviceBoardData;
    return (
      <>
        <Link
          to={{
            pathname: `/board/${serviceBoardData.id}`,
            serviceBoardData: serviceBoardData,
          }}
        >
          <article
            className={
              serviceBoardData.progress
                ? "articleUBA3"
                : serviceBoardData.ansUser
                ? "articleUBA1"
                : "articleUBA2"
            }
          >
            {serviceBoardData.progress ? (
              <h1 className="colorblue2"> </h1>
            ) : serviceBoardData.ansUser ? (
              <h1 className="colorblue2"></h1>
            ) : (
              <h1 className="colorblue2"></h1>
            )}
            <span className="colorblue"></span> : {serviceBoardData.title}
            <br />
            <span className="colorblue"></span> : {serviceBoardData.contents}
            <br />
            <span className="colorblue"></span> : {serviceBoardData.price}
            <br />
            <span className="colorblue"></span> : {serviceBoardData.address1}, {serviceBoardData.address2}
            <br />
            <span className="colorblue"></span> : {serviceBoardData.startAt}
            <br />
            <span className="colorblue"></span> : {serviceBoardData.endAt}
            <br />
            <span className="colorblue"></span>: {serviceBoardData.reqUser.nickName}
          </article>
        </Link>
      </>
    );
  }
}

export default UserBoardArticle;
