import React, { Component } from "react"
import { Link } from "react-router-dom"
import AlarmPage from "./AlarmPage"

class Aside extends Component {
  render() {
    return (
      <>
        <aside>
          <Link to="/Write"><div className="asider4"><img className="asiderpng" src="/write.png" width="px" title="" alt=""></img></div></ Link>
          <Link to="/message"><div className="asider2"><img className="asiderpng" src="/message.png" width="px" title="" alt=""></img></div></ Link>
          <AlarmPage />
          <Link to="/userInfor"><div className="asider1"><img className="asiderpng" src="/user.png" width="px" title="" alt=""></img></div></ Link>
        </aside>
      </>
    )
  }
}
export default Aside